body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
}

.nav-container a {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}

footer {
  text-align: center;
  padding: 20px;
  background-color: #333;
  color: #fff;
}
